        </el-row>
        <el-row type="flex" class="row-bg" justify="center">
          <el-col >
            <div style="font-size:24px;">
              Recently, text-guided image generation by diffusion models has made astonishing progress, achieving exciting effects in open-domain image manipulation tasks. However, due to the complexity and diversity of image manipulations, few models currently possess comprehensive zero-shot abilities for both global and local image editing. In this work, we propose a method with a mixture-of-expert controllers to align the text-guided capacity of diffusion models with different kinds of human instructions, enabling our model to handle various open-domain image manipulation tasks with natural language instructions. First, in addition to the instruction-based image editing dataset, we employ the generative capabilities of large language models (Chatgpt) and conditional image synthesis models (ControlNet) to build a large number of global image transfer datasets. Then, to adapt to different image manipulation tasks, we use a mixture-of-expert technique for ability adaptation. Our conditional diffusion model undergoes task-specific adaptation training on a comprehensive dataset and can achieve global and local image editing capabilities during the inference phase without additional fine-tuning. Extensive experiments demonstrate that our method achieves surprisingly good performance on various image manipulation tasks with open-domain images and human instructions.
            </div>
